*{
    padding: 0;
    margin: 0;
    font-family:"jost","sans-serif";
}
body{
    background-image: linear-gradient( rgba(175, 11, 74, 0.815), rgba(5, 92, 107, 0.774));
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    height: 100vh;
    width: 100vw;
    
}
html,
body{
    font-size: 62.5%;
    height: 100%;
}
html{
    background: rgb(211, 188, 187);
}
.box{
    width: 20vw;
    height: 60vh;
    border-radius: 1rem;
    box-shadow: 0 0.2rem rgba(0, 0,0, 0.2);
    background: #46b9c9a4;
    position: relative;
    overflow: hidden;
    transform: translate3d(0,0,0);
    min-width: 20rem;
    min-height: 35rem;
}
.inputData{
  margin-top: 20px;
  position: relative;
  display: flex;
  justify-content: center;
}
.inputField{
    border-radius: 2rem;
    height: 40px;
    text-align: center;
    font-size: 2rem;
    font-weight: 300;
}
.wave{
    opacity: 0.3;
    position: absolute;
    top: 120%;
    left: 50%;
    background: white;
    width: 50rem;
    height: 50rem;
    margin-left: -25rem;
    margin-top: -25rem;
    transform-origin: 50% 48%;
    border-radius: 43%;
    animation: drift 3000ms infinite linear;
    z-index: 1 !important;
}
.wave-three{
    animation: drift 5000ms infinite linear;
    z-index: 2 !important;
    opacity: 0.2;
}
.wave-three{
    animation: drift 500ms infinite linear;
    z-index: 2 !important;
    opacity: 0.2;
}
.wave-two{
    animation: drift 700ms infinite linear;
    opacity: 0.1;
    z-index: 3 !important;
}
.errorMsg{
    text-align: center;
    font-weight: 400;
    font-size: 3rem;
}
.box:after{
    content: "";
    display: block;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 11;
    transform: translate(0,0,0);
}
@keyframes drift{
    from{
        transform: rotate(0deg);
    }
    from{
        transform: rotate(360deg);
    }
}
.info{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 45%;
    z-index: 4;
}
.location{
    margin: 1.5rem;
    text-align: center;
    font-weight: 800;
    font-size: 5rem;
}
.fa-street-view{
    animation: rotate 3s linear infinite alternate;
}
@keyframes rotate{
    from{
        transform: translateX(-0.5rem);
    }
    to{
        transform: translateX(0.5rem);
    }
}
#date{
    text-align: center;
    margin-top: 0.5rem;
    color: #57606f;
    font-size: 1.2rem;
    font-weight: 300;
    text-transform: uppercase;
}
.temp{
    margin-top: 2.5rem;
    text-align: center;
    font-size: 3rem;
}
.temmin_max{
    text-align: center;
    margin-top: 0.3rem;
    font-weight: 500;
    font-size: 1.5rem;
    color: #232324;
}
#weathercon{
    height: 55%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3rem;
}
#weathercon .fas{
    font-size: 6rem;
    animation: fas-anime 3s linear infinite alternate;
}
@keyframes fas-anime{
    from{
        transform: scale(0.8);
    }
    to{
        transform: scale(1.2);
    }
}
@media(max-width:600)
{
    .box{
        width: 90vw;
        height: 80vh;
    }
    .wave{
        top: 80%;
    }
    #weathercon{
        font-size: 5rem;
    }
    .info{
        font-size: 1.5rem;
    }
}
@media(max-width:500)
{
    .box{
        height: 50vh;
    }
    .wave{
        top: 115%;
    }
    #weathercon{
        font-size: 5rem;
    }
}
body>span{
    width: 100vw;
    text-align: center;
    color: gray;
}
